import React, { Component } from 'react'
import {BrowserRouter as Router, Route} from 'react-router-dom' 
import './App.css'
import Todos from './components/Todos.js'
import Header from './components/layouts/Header.js'
import AddTodo from './components/AddTodo.js'
import About from './components/pages/About.js'
// import uuid from 'uuid'
import Axios from 'axios'

class App extends Component {
  state = {
    todos : []
  }

  componentDidMount() {
    Axios.get('https://jsonplaceholder.typicode.com/todos?_limit=10').then(res=>this.setState({todos : res.data})) 
  }

  //toggle complete
  markComplete = (id) =>{
    this.setState({todos : this.state.todos.map(todo => {
      if(todo.id === id){
        todo.completed = !todo.completed;
      }
      return todo;
    })})
  }

  //delete todo
  delTodo = (id) =>{
    Axios.delete(`https://jsonplaceholder.typicode.com/todos/${id}`).then(res => this.setState({todos : [...this.state.todos.filter(todo => todo.id !== id)]}));
  }

  //add new todo task
  addTodo = (title)=>{
    Axios.post('https://jsonplaceholder.typicode.com/todos',{
      title,
      completed : false
    }).then(res=>this.setState({todos : [...this.state.todos,res.data]}));
    // const newTodo = {
    //   id :uuid.v4(),
    //   title,
    //   completed : false
    // }
    // this.setState({todos : [...this.state.todos,newTodo]})
  }
  render() {
    return (
      <Router>
        <div className="App">
          <div className="container">
            <Header />
             {/* if we dont use exact then this will show in every route but we want to show only when we visit '/' route */}
            <Route exact path='/react' render={props => (
              <React.Fragment>
                <AddTodo addTodo={this.addTodo}/>
                <Todos todos= {this.state.todos} markComplete = {this.markComplete} delTodo = {this.delTodo}/>              
              </React.Fragment>
              )} />
              <Route path='/about' component={About} />
          </div>
        </div>
      </Router>
    );
  }
}

export default App;
